.sidebar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: $white;
    margin-top: 0;
    z-index: 1001;
    @include position(fixed, 56px, null, 0, 0);
    @include transition(all 0.5s ease);
    width: 276px;

    @include respond-below(custom991) {
        margin-left: -575px;
        @include transition(all 0.4s ease);
        z-index: 1041;
        background: $white;
    }

    @include respond-below(custom575) {
        width: 100%;
    }

    .slimScrollDiv {
        border-right: 1px solid transparent;

        @include respond-below(custom575) {
            width: 100% !important;
        }
    }

    .sidebar-menu {
        @include margin-padding(null, 15px);

        .clinicdropdown {
            margin-bottom: 24px;

            >a {
                background: $light-300;
                border-radius: $border-radius-lg;
                padding: 10px;

                img {
                    width: 42px;
                    height: 42px;
                    border-radius: $border-radius-lg;
                }

                .user-names {
                    margin-left: 10px;
                    flex: 1;

                    h5 {
                        font-size: $font-size-16;
                        font-weight: $font-weight-semibold;
                        color: $title-color;
                        margin-bottom: 2px;
                    }

                    h6 {
                        font-size: $font-size-14;
                        font-weight: $font-weight-normal;
                        color: $text-color;
                        margin-bottom: 0;
                    }
                }
            }

            // a:hover{
            //     background-color: red;
            // }
        }

        .submenu-two {
            .subdrop.active {
                background: transparent;
            }
        }

        >ul {
            >li {
                margin-bottom: 24px;

                &.active {
                    a {
                        color: $white;
                    }

                    svg {
                        color: #FE9F43
                    }
                }

                svg {
                    min-width: 16px;
                    width: 16px;
                    height: 16px;
                    margin-right: 10px;
                }

                >a {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    align-items: center;
                    -webkit-box-align: center;
                    -ms-flex-align: center;
                    @include margin-padding(null, 10px 15px);
                    position: relative;
                    color: $secondary;

                    svg {
                        color: #637381;
                    }

                    img {
                        color: #637381;
                    }

                    &.active {
                        background: rgba(254, 159, 67, 0.08);
                        color: #FE9F43;
                        border-radius: 5px;

                        svg {
                            color: $white;
                        }

                        img {
                            filter: invert(72%) sepia(76%) saturate(1430%) hue-rotate(327deg) brightness(103%) contrast(101%);
                        }

                        span {
                            color: #FE9F43;
                        }

                        .menu-arrow {
                            background: #FFEDDC;

                            &::before {
                                border-color: #FE9F43;
                            }
                        }
                    }

                    span {
                        font-size: $font-size-15;
                        font-weight: $font-weight-medium;
                        color: $gray-600;
                    }
                }

                &.active {
                    a {
                        background: rgba(254, 159, 67, 0.08);
                        border-radius: 5px;

                        img {
                            filter: invert(72%) sepia(76%) saturate(1430%) hue-rotate(327deg) brightness(103%) contrast(101%);
                        }

                        span {
                            color: #FE9F43;
                        }
                    }
                }

                &.submenu {
                    ul {
                        display: none;

                        li {
                            a {
                                font-weight: $font-weight-medium;
                                font-size: $font-size-base;
                                position: relative;
                                display: block;
                                @include margin-padding(null, 8px 8px 8px 40px);
                                padding-left: 30px;
                            }
                        }
                    }
                }

                ul {
                    display: block;
                    // margin-bottom: 24px;

                    li {
                        margin-bottom: 5px;
                        border-radius: 5px;
                        overflow: hidden;

                        a {
                            display: -webkit-box;
                            display: -ms-flexbox;
                            display: flex;
                            align-items: center;
                            -webkit-box-align: center;
                            -ms-flex-align: center;
                            font-weight: $font-weight-normal;
                            font-size: $font-size-14;
                            color: $gray-400;
                            position: relative;
                            width: 100%;
                            @include margin-padding(null, 8px);

                            i {
                                color: $gray-900;
                                font-size: $font-size-16;
                                background: $light-300;
                                border-radius: 5px;
                                width: 26px;
                                height: 26px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                            }

                            span {
                                font-size: $font-size-15;
                                font-weight: $font-weight-medium;
                                white-space: nowrap;
                                color: $gray-900;
                                margin-left: 10px;
                            }

                            &.active {
                                color: $primary;
                                background: $primary-100;
                                border-radius: $border-radius-lg;

                                span {
                                    color: $primary;
                                }

                                i {
                                    background: $primary;
                                    color: $white;
                                }
                            }

                            &:hover {
                                color: $primary;

                                span {
                                    color: $primary;
                                }
                            }
                        }
                    }

                    >li {
                        &.active a {
                            &.subdrop {
                                background: #FFEEEC;
                                border-radius: 5px;
                                color: #E41F07;

                                i {
                                    color: $white;
                                    background: #E41F07;
                                }

                                span {
                                    color: #E41F07;

                                }
                            }
                        }
                    }
                }

                .submenu-hdr {
                    font-weight: $font-weight-semibold;
                    font-size: $font-size-12;
                    color: $gray-400;
                    margin: 0 0 14px 0;
                    text-transform: uppercase;
                }

                &:first-child {
                    h6 {
                        margin-top: 0;
                    }
                }

                // .submenu {
                //     ul {
                //         display: none;
                //         border: 0;
                //         padding-top: 0;
                //         padding-bottom: 0;
                //     }
                // }
            }

            li {
                margin-bottom: 5px;

                .submenu> {
                    ul {
                        margin: 0;

                        li {
                            a {
                                padding-left: 44px;
                                font-size: $font-size-base;
                                background: transparent;
                                position: relative;

                                &:after {
                                    position: absolute;
                                    content: "";
                                    background-color: #898989;
                                    width: 7px;
                                    height: 7px;
                                    border-radius: 50px;
                                    left: 16px;
                                    top: 14px;
                                }

                                &:before {
                                    position: absolute;
                                    content: "";
                                    background-color: #898989;
                                    width: 0.5px;
                                    height: 8px;
                                    left: 19px;
                                    top: 0px;
                                }
                            }
                        }
                    }

                    a {
                        &.subdrop {
                            background: $light-200;

                            i {
                                background: $dark;
                                color: $white;
                            }

                            &.submenu-two {
                                background: transparent;
                            }
                        }

                        &.active {
                            background: $primary-100;
                            border-radius: 5px;

                            i {
                                background: $primary;
                                color: $white;
                            }

                            span {
                                color: $primary;
                            }

                            .menu-arrow {
                                &::before {
                                    border-right: 2px solid $primary;
                                    border-bottom: 2px solid $primary;
                                }
                            }
                        }
                    }
                }

            }

        }

        .submenu-open {
            &:last-child {
                margin-bottom: 0;

                ul {
                    border-bottom: none;
                    padding-bottom: 0;
                }
            }

            .submenu {
                &.submenu-two {
                    ul {
                        li {
                            a {
                                margin-left: 45px;
                            }
                        }
                    }

                    &.submenu-three {
                        ul {
                            li {
                                a {
                                    margin-left: 65px;
                                }
                            }
                        }
                    }
                }

                ul {
                    li {
                        a {
                            margin-left: 25px;
                            padding-bottom: 4px;
                            font-size: $font-size-base;
                            color: $sub-title;

                            &::after {
                                content: "";
                                background: rgba(50, 71, 92, 0.38);
                                width: 8px;
                                height: 8px;
                                border: 2px solid $white;
                                @include rounded(50px);
                                @include position(absolute, 16px, null, null, 0);
                            }

                            &:hover {
                                color: $primary;

                                &:after {
                                    background: #FE9F43;
                                    border: 2px solid #FDB;
                                }
                            }
                        }
                    }
                }
            }
        }

        .menu-arrow {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            justify-content: center;
            -webkit-justify-content: center;
            -ms-flex-pack: center;
            text-rendering: auto;
            line-height: 40px;
            font-size: $font-size-18;
            line-height: 16px;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            @include position(absolute, 12px, 10px, null, null);
            @include transform(translate(0, 0));
            @include transition(all 0.2s ease);

            &::before {
                height: 8px;
                width: 8px;
                border-top: 0;
                content: "";
                border-left: 0;
                border-right: 2px solid $gray-900;
                border-bottom: 2px solid $gray-900;
                transform: rotate(-45deg);
                position: absolute;
                left: 5px;
                top: 5px;
            }

            &.inside-submenu {
                right: 10px;

                &.inside-submenu-two {
                    right: 60px;
                }
            }
        }

        &.subdrop {
            .menu-arrow {
                @include transform(rotate(90deg));
                background: #FFEDDC;
            }
        }
    }

    .nav-link {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        justify-content: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        @include margin-padding(null, 10px);
        color: $secondary;
        position: relative;

        &.active {
            color: $primary;
            background: transparent;
            position: relative;

            &:after {
                content: "";
                border-width: 10px;
                border-color: transparent $primary transparent transparent;
                border-style: solid;
                @include position(absolute, 36px, 0, null, null);
            }
        }

    }

    .sidebar-left {
        width: 100px;
        @include box-shadow(null, 5px, 0px, 15px, null, rgba(0, 0, 0, .05));
    }

    .sidebar-right {
        width: 250px;
        height: 100%;
        @include transition(all 0.2s ease);
        display: none;

        @include respond-below(custom991) {
            width: calc(100% - 70px);
        }

        .slimScrollDiv {
            width: 250px !important;
            overflow: visible !important;

            @include respond-below(custom991) {
                width: 100% !important;
            }
        }

        .tab-content {
            @include margin-padding(null, 20px 20px 40px);

            @include respond-below(custom991) {
                @include margin-padding(null, 10px);
            }
        }

        p {
            font-size: $font-size-10;
            color: $gray-500;
            text-transform: uppercase;
            font-weight: $font-weight-bold;
            margin-bottom: 10px;
        }

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            li {
                &.active {
                    a {
                        background: linear-gradient(46.62deg, $primary 0%, $violet 93.64%);
                        color: $white;
                    }
                }

                a {
                    font-weight: $font-weight-normal;
                    font-size: $font-size-base;
                    color: $secondary;

                    &.active {
                        color: $primary;
                    }
                }
            }
        }

        .menu-arrow {
            -webkit-transition: -webkit-transform 0.15s;
            -o-transition: -o-transform 0.15s;
            transition: transform .15s;
            display: inline-block;
            font-family: 'Font Awesome 5 Free';
            text-rendering: auto;
            line-height: 40px;
            font-size: $font-size-18;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            line-height: 18px;
            top: 0px;
            position: relative;
            font-weight: $font-weight-bold;
            @include transform(translate(0, 0));

            &:before {
                content: "\f105";

            }
        }
    }

    .sub-menu a {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        -ms-flex-pack: space-between;
    }

    li {
        a.subdrop {
            .menu-arrow {
                @include transform(rotate(90deg));
            }
        }
    }

    .nav-items {
        .nav-link {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
            flex-direction: column;
            @include rounded(0);
            border-bottom: 1px solid $gray-400;
            @include margin-padding(null, 20px 0);
            @include transition(all 0.4s ease);

            &:hover {
                box-shadow: 0 0 0 150px $violet inset;
                color: $white;
            }

            span {
                margin-top: 15px;
                font-weight: $font-weight-medium;
            }
        }

    }

    .sidemenu {
        a {
            width: 100%;
            font-weight: $font-weight-medium;
            display: block;
            @include rounded(5px);
            @include margin-padding(0 0 10px, 10px);

            &:hover {
                background: linear-gradient(46.62deg, $primary 0%, $violet 93.64%);
                color: $white;

                svg {
                    color: $white;
                }
            }

            svg {
                stroke-width: 1px;
            }

        }
    }
}


@include respond-above(custom991) {
    .mini-sidebar {
        .page-wrapper {
            margin-left: 80px;
        }

        .user-names {
            display: none;
        }

        .header-left {
            #toggle_btn {
                opacity: 1;
                transform: rotate(180deg);

                &:after {
                    border: 0;
                }
            }
        }

        .sidebar {
            .sidebar-menu {
                .clinicdropdown>a {
                    padding: 5px;
                }
            }
        }

        &.expand-menu {
            .header-left {
                #toggle_btn {
                    opacity: 1;
                    transform: rotate(180deg);
                }
            }

            .sidebar {
                .sidebar-menu {
                    .clinicdropdown>a {
                        padding: 10px;
                    }
                }
            }
        }

        .sidebar-right {
            display: none;
        }

        .sidebar {
            .sidebar-menu {
                ul {
                    li {
                        a {
                            span {
                                display: none;
                            }
                        }

                        .submenu-hdr {
                            display: none;
                        }

                        svg {
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .sidebar {
            width: 80px;
            overflow: hidden;
        }

        &.expand-menu {
            .sidebar {
                display: block
            }
        }
    }

    .expand-menu {
        &.mini-sidebar {
            .sidebar {
                .sidebar-menu {
                    .submenu-hdr {
                        display: block;
                    }
                }

                .user-names {
                    display: block;
                }
            }
        }

        .sidebar-right {
            display: block;
            @include transition(all 0.2s ease);
        }

        .sidebar {
            width: 276px;

            .sidebar-menu {
                ul {
                    >li {
                        >a {
                            span {
                                display: inline-block;
                                line-height: 0;
                            }
                        }

                        svg {
                            margin-right: 10px;
                        }
                    }
                }

                .submenu-hdr {
                    display: block;
                }
            }
        }

        .header-left {
            width: 276px;
        }
    }

    #toggle_btn {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        justify-content: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        float: left;
    }
}

body.mini-sidebar.expand-menu {
    .sidebar .sidebar-menu>ul>li ul li a {
        justify-content: start;
    }
}

body.mini-sidebar {
    .sidebar .sidebar-menu>ul>li ul li a {
        justify-content: center;
    }

    .sidebar {
        .sidebar-menu>ul>li.submenu-open ul li a {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            justify-content: center;
            -webkit-justify-content: center;
            -ms-flex-pack: center;
            font-weight: $font-weight-medium;
            font-size: $font-size-15;
            color: #67748E;
            position: relative;
            width: 100%;
            padding: 10px;
        }

        .sidebar-menu>ul>li ul>li.active a {
            padding: 10px;
            justify-content: center;
        }
    }

    &.expand-menu {
        .sidebar {
            .sidebar-menu>ul>li.submenu-open ul li a {
                justify-content: left;
                -webkit-justify-content: left;
                -ms-flex-pack: left;
                padding-left: 18px;
            }

            .sidebar-menu>ul>li ul>li.active a {
                padding: 10px 15px;
                justify-content: start;
            }
        }
    }
}

.sidebarrightmenu {
    .sidebar-right {
        display: block;
    }
}

.slide-nav {
    .sidebar {
        margin-left: 0;
    }
}

.sidebar-overlay {
    display: none;
    height: 100%;
    width: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
    @include position(fixed, 60px, null, null, 0);

    &.opened {
        display: block;
    }
}

.mini-sidebar {
    .active.subdrop~ul {
        display: none !important;
    }
}

.mini-sidebar.expand-menu {
    .active.subdrop~ul {
        display: block !important;
    }
}

.active.subdrop~ul {
    display: block !important;
}

.sidebar {
    .sidebar-menu {
        >ul {
            li {
                .submenu {
                    &.submenu-two {
                        >ul {
                            li {
                                a {
                                    margin-left: 30px;
                                }
                            }
                        }

                        &.submenu-three {
                            >ul {
                                li {
                                    a {
                                        margin-left: 60px;
                                    }
                                }
                            }
                        }
                    }
                }

            }
        }
    }
}

.mini-sidebar {
    .header {
        #toggle_btn {
            opacity: 0;
            height: 0;
